

import { defineComponent } from 'vue'
export default defineComponent({
    name: "BaseChatInput",
    props: {
        modelValue: {
            type: String,
            required: false,
            default: ''
        },
        maxHeight: {
            type: Number,
            required: false,
            default: 180
        },
        minHeight: {
            type: Number,
            required: false,
            default: 64
        },
        chatPartner: {
            type: String,
            required: false,
            default: ''
        }
    },
    data: () => ({
        textarea: null as unknown as HTMLTextAreaElement,
        height: '',
        focused: false
    }),
    methods: {
        calculateStyles(): void {
            const roundHeight = (h) => {return h > this.maxHeight ? this.maxHeight : h}
            this.textarea.style.height = this.minHeight + 'px' || 'auto'
            this.textarea.style.height = roundHeight(this.textarea.scrollHeight) + 'px'
        },
        onInput(e) {
            this.calculateStyles()
            this.$emit('update:modelValue', e.target.value)
        },
        onEnter(e) {
            this.$emit('enter', e.target.value)
            this.$emit('update:modelValue', '')
            this.$nextTick(() => {
                this.calculateStyles()
            })  
        },
        onFocus() {
            this.focused = true
        },
        pushEnter() {
            if(!this.modelValue) return 
            this.$emit('update:modelValue', this.modelValue + '\n')
            this.$nextTick(() => {
                this.textarea.scrollTop = this.textarea.scrollHeight
                this.calculateStyles()
            })
            
        }
    },
    mounted() {
        this.textarea = this.$refs.textarea
        this.calculateStyles()
    },
    computed: {
        placeholder(): string {
            return this.chatPartner 
                    ? `Написать ${this.chatPartner}`
                    : 'Введите сообщение...'
        }
    }
})
